import React from 'react';
import styled, { css } from 'styled-components';
import GatsbyImage from 'gatsby-image';
import { github, hero } from '../../constants';

const Container = styled('div')`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const preStyles = ({ theme }) => css`
  margin: 0;
  align-self: end;
  color: ${theme.colors.p600};
  font-size: 20px;
  font-weight: ${theme.fontWeight.bold};
`;

const PreTitle = styled('p')`
  ${preStyles}
`;

const titleStyles = ({ theme }) => css`
  color: ${theme.colors.p600};
  font-size: 75px;
  font-weight: ${theme.fontWeight.bold};
  margin: 0;

  ${theme.mq.medium`
    margin: 0 10px 20px;
    font-size: 90px;
    font-weight: ${theme.fontWeight.medium};
  `};
`;

const Title = styled('h1')`
  ${titleStyles}
`;

const subTitleStyles = ({ theme }) => css`
  color: ${theme.colors.p600};
  font-size: 23px;
  font-weight: ${theme.fontWeight.bold};
`;

const SubTitle = styled('p')`
  ${subTitleStyles}
`;

const imageStyles = ({ theme }) => css`
  border-radius: 50%;
  left: 0;
  top: 0;
  border: 3px solid ${theme.colors.p600};
  margin: 0;
`;

const Image = styled(GatsbyImage)`
  ${imageStyles}
`;

function Hero({ avatar }) {
  return (
    <Container>
      <Image fixed={avatar.childImageSharp.fixed} alt={github.MY_USERNAME} />
      <PreTitle>{hero.HEY}</PreTitle>
      <Title>{hero.IM_GUY}</Title>
      <SubTitle>{hero.WHAT_I_DO}</SubTitle>
    </Container>
  );
}

export default Hero;
